#top-nav {
  height: 50px;
  width: 100%;
  border-bottom: 1px solid #bbb;
  -webkit-transform: translateX(0);
          transform: translateX(0);
  transition: 1s all;
}

#top-nav {
  display: flex;
  align-items:center;
}

#top-nav span {
  position: absolute;
  right: 10%;
}

#nav-panel {
  position: absolute;
  top: 0;
  z-index: 6;
  width: 350px;
  height: 100vh;
  border-right: 1px solid #161626;
  left: -370px;
  -webkit-transform: translateX(0);
          transform: translateX(0);
  transition: .5s all;
  background: white;
  color: teal;
}

#nav-panel.active {
  -webkit-transform: translateX(370px);
          transform: translateX(370px);
}

#nav-panel.closed {
  -webkit-transform: translateX(-370px);
          transform: translateX(-370px);
}

body {
    margin: 0;
    padding: 0;
    min-height: 100vh;
    min-width: 100vw;
    background-color: #fff;
}

.backdrop {
  background: grey;
  opacity: 0;
  z-index: 5;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  transition: opacity .25s ease, height 0s linear 0s;
}

.backdrop.active {
  opacity: 0.8;
}

.flex-jc {
  display:flex;
}

.jc {
  justify-content: center;
}

.p-l {
  padding: 4px;
}

.content-container {
    padding-left: 5em;
}

